@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
/* colors */
.header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 50vh;
  padding-top: 8em; }
  .header::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    background: #fff;
    background-image: url(/static/media/head.96624a7e.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; }
  .header__content {
    text-align: center;
    color: #fff; }
    .header__content span {
      text-transform: uppercase;
      letter-spacing: 0.07em;
      cursor: pointer; }
      .header__content span {
        font-size: 0.889rem; }
        @media screen and (min-width: 20rem) {
          .header__content span {
            font-size: calc(-0.21262vw + 0.93152rem); } }
        @media screen and (min-width: 85.375rem) {
          .header__content span {
            font-size: 0.75rem; } }
  .header__title {
    margin: 0;
    padding-bottom: 0.2em;
    text-transform: capitalize;
    color: #fff; }

@media (max-width: 599px) {
  .header {
    padding-top: 0;
    height: 50vh; }
    .header__title {
      font-size: 1.424rem; } }
    @media screen and (max-width: 599px) and (min-width: 20rem) {
      .header__title {
        font-size: calc(1.44551vw + 1.1349rem); } }
    @media screen and (max-width: 599px) and (min-width: 85.375rem) {
      .header__title {
        font-size: 2.369rem; } }

@media (max-width: 599px) {
    .header__content span {
      font-size: 0.702rem; } }
    @media screen and (max-width: 599px) and (min-width: 20rem) {
      .header__content span {
        font-size: calc(-0.4283vw + 0.78766rem); } }
    @media screen and (max-width: 599px) and (min-width: 85.375rem) {
      .header__content span {
        font-size: 0.422rem; } }

@media (min-width: 1800px) {
  .header {
    padding-top: 12em; }
    .header__title {
      font-size: 1.602rem; } }
    @media screen and (min-width: 1800px) and (min-width: 20rem) {
      .header__title {
        font-size: calc(2.37859vw + 1.12628rem); } }
    @media screen and (min-width: 1800px) and (min-width: 85.375rem) {
      .header__title {
        font-size: 3.157rem; } }

@media (min-width: 1800px) {
    .header__content span {
      font-size: 1.125rem; } }
    @media screen and (min-width: 1800px) and (min-width: 20rem) {
      .header__content span {
        font-size: calc(0.31816vw + 1.06137rem); } }
    @media screen and (min-width: 1800px) and (min-width: 85.375rem) {
      .header__content span {
        font-size: 1.333rem; } }

